<template>
    <div>
        <zoomMeetingSdk class="zoomVideoComponent" :meetingInfo="meetingInfo"
                        :forceOutActive="forceOutActive"
                        :hostEndActive="hostEndActive"
                        :userInfo="userInfo"
                        @zoomAction="zoomAction"
                        v-if="meetingInfoShow" role="1"></zoomMeetingSdk>
        <div id="zoom-live-pc">
            <hostVideoComponent :meetingInfo="meetingInfo" :meetingStart="meetingStart" v-if="meetingInfoShow"  @outActive="outActive"></hostVideoComponent>
        </div>
        <iframe :src="testDebugUrl" id="testIframe" style="display:none" v-if="DEBUG && testDebugUrl"></iframe>
    </div>

</template>


<script>
    import zoomMeetingSdk from "@/views/WEB/liveStream/zoomWebMeeting/zoomMeetingSdk";
    import hostVideoComponent from "@/views/WEB/liveStream/hostLive/component/hostVideoComponent";
    import AUTH from "@/config/web/auth";
    import hostLiveHandler from "@/views/WEB/liveStream/handler/hostLiveHandler";
    import hostLiveComuHandler from "@/views/WEB/liveStream/handler/hostLiveComuHandler";
    import JQ from "jquery";
    import apiService from "@/config/web/apiService";
    import {API_URL_BROADCAST_UPDATE_HOST_START_TIME} from "@/config/web/urls";

    export default {
        name: 'hostVideoPc',
        components: {
            zoomMeetingSdk,
            hostVideoComponent,

        },
        data() {
            return {
                DEBUG: ((process.env.NODE_ENV == 'development') ? true : false),
                id: '',
                testDebugUrl: '',
                forceOutActive: false,
                hostEndActive: false,
                meetingStart:false,
                userInfo: {},
                meetingInfo: {},
                meetingInfoShow: false,

            }
        },
        created() {
            this.id = this.$route.params.id;
            if (this.$isMobile()) {
                location.replace('/liveStream/hostVideoMobile/' + this.id)
                return;
            }
            AUTH.setDevice(this.$isMobile());
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                if (this.id < 1) {
                    this.doError();
                    return;
                }

                this.memberInfo = member.memberInfo;
                this.uid = this.memberInfo.uid;
                this.userInfo = {
                    uid: this.uid,
                    userName: this.memberInfo.name,
                    userEmail: this.memberInfo.email
                }

                this.streamRoomId = this.id + '_' + this.uid;
                this.getMeetingInfo();

            } else {
                console.log('error')

            }
        },
        methods: {
            /**** 방송 시작 부분 ****/
            getMeetingInfo() {
                const params = {
                    id: this.id.toString(),
                    uid: this.uid,
                }
                hostLiveHandler.init(params, this, this.DEBUG);
                hostLiveComuHandler.init(params, this);
                hostLiveHandler.getLiveInfo((ret) => {
                    this.meetingInfo = ret;
                    this.meetingInfoShow = true;

                })
            },
            zoomAction(params) {
                switch (params.type) {
                    case 'start':

                        JQ('#screen-init').hide()
                        this.meetingStart = true;
                        if (this.DEBUG) {
                            const params = {id: this.id.toString()}
                            apiService.post(API_URL_BROADCAST_UPDATE_HOST_START_TIME, params, (ret) => {

                            })

                            this.testDebugUrl = 'http://localhost/testJsonRpc/' + this.id + '/' + this.uid;
                        } else {
                            hostLiveHandler.joinSocketLiveStream();
                        }
                        break;
                }
            },
            outActive() {
                this.forceOutActive = true;
            },

        },
        mounted() {

        }
    }
</script>
